import React, { Component } from 'react';
// setINterval를 활용한 가위바위보
// 리액트 라이프 사이클
// 클래스의 경우 -> constructor -> render -> ref ->componentDidMount
// (setState/props 바뀔때) -> shouldComponentUpdate(true) ->render -> componentDidUpdate
// 부모가 자식 컴포넌트를 없앴을 때 -> componentWillUnmount -> 소멸
const rspCoords = {
  바위:'0',
  가위: '-142px',
  보: '-284px'
}

const scores = {
  가위: 1,
  바위: 0,
  보: -1
}
const computerChoice = (imgCoord) =>{
  return Object.entries(rspCoords).find(function(v){
    return v[1] === imgCoord;
  })[0];
};




class RSP extends Component {
  state = {
    result : '',
    score : 0,
    imgCoord : '0',
  }
  interval;

  changeHand = () => { 
    const { imgCoord } = this.state; 
    if (imgCoord === rspCoords.바위) {
      this.setState({
        imgCoord: rspCoords.가위
      })
    } else if ( imgCoord === rspCoords.가위){
      this.setState({
        imgCoord: rspCoords.보
      })
    } else if (imgCoord === rspCoords.보) {
      this.setState({
        imgCoord: rspCoords.바위
      })
    }
  }
  // 컴포넌트가 DOM에 붙고 나서 특정한 동작을 수행하도록 하는것: componenetDidMount!
  // render후 수행하는 것
  componentDidMount() { //컴포넌트가 첫 렌더링된 후, 여기에 비동기 요청을 많이 한다.
    this.interval = setInterval(this.changeHand,100);
  }

  componentDidUpdate() { //리렌더링 후 실행

  }

  componentWillUnmount() { // 컴포넌트 didmount에서 했던 작업들을 제거하는 것, 여기에서 비동기 작업 제거!
    console.log('unMount~');
    clearInterval(this.interval); // 메모리 누수 문제 해결
  }

  onClickBtn = (choice) => (e) => { // 자주쓰는 패턴 curried function
    console.log(choice);
    console.log(e)
    const { imgCoord } = this.state
    clearInterval(this.interval);
    const myScore = scores[choice];
    const cpuScore = scores[computerChoice(imgCoord)];
    const diff = myScore - cpuScore;
    if (diff === 0) {
      this.setState({
        result:'비겼습니다!'
      });
    } else if ([-1,2].includes(diff)) {
      this.setState((prevState) => {
        return {
          result: '이겼습니다.!',
          score: prevState.score +1,
        };
      });
    } else {
      this.setState((prevState) => {
        return {
          result: '졌습니다!',
          score: prevState.score - 1,
        };
      });
    }
    setTimeout(()=> {
      this.interval = setInterval(this.changeHand, 100);
    }, 2000);
  };
  render() {
    const { result, score, imgCoord} = this.state;
    return (
      <>
      <h1>가위바위보!</h1>
      <div id="computer" style={{ background: `url(https://en.pimg.jp/023/182/267/1/23182267.jpg) ${imgCoord} 0` }}></div>
      <div>
        <button id='rock' className="btn" onClick={this.onClickBtn('바위')}>바위</button>
        <button id='scissor' className="btn" onClick={this.onClickBtn('가위')}>가위</button>
        <button id='paper' className="btn" onClick={this.onClickBtn('보')}>보</button>
      </div>
      <div>{result}</div>
      <div>현재 {score}점</div>
      </>
      )
  }
}

export default RSP;